<template>
  <div :class="{'login-container':isPc, 'login-container-mobile' : !isPc}">
    <div class="content-box">
      <span class="login-title">{{$t('Login.Login-Panel')}}</span>
      <ul class="all-tips">
        <li class="row-tips">auVPN 不使用密碼登入。</li>
        <li class="row-tips">登入鏈接為登入唯一途徑。</li>
      </ul>
      <div class="browser-container" :class="{'email-container': !isEmail}">
        <div class="browser-box" :class="{'chosen-browser': isBrowser, 'browser-bg': !isBrowser}" :style="{'z-index': isBrowser? 1 :0}"
             @click="modeSwitch(true)">
          <div class="client-header">
            <span class="header-title">{{$t('Login.Address-Login-Describe')}}</span>
          </div>
          <div class="client-info" v-show="isBrowser">
            <div class="tips-span">{{$t('Login.Copy-Address')}}</div>
            <lottie :options="defaultOptions" v-on:animCreated="handleAnimation"
                    style="overflow: visible;margin: -24px 0 -20px 0;"/>
          </div>
        </div>
        <div v-if="isEmail" class="email-box" :class="{'chosen-email': !isBrowser && canSwitch, 'email-bg': isBrowser}"
             :style="{'z-index': isBrowser? 0:1, 'cursor': canSwitch?'pointer': 'not-allowed'}" @click="modeSwitch(false)">
          <div class="client-header">
            <span class="header-title email-header">{{$t('Login.Login-Address')}}</span>
          </div>
          <div class="client-info" v-show="!isBrowser">
            <div class="tips-span">{{$t('Login.If-Tired-Email')}}</div>
            <div class="login-email-box">
              <a-input class="email-input" :placeholder="$t('Login.Enter-Email-Placeholder')" v-model="email" @change="clearTipsMsg" v-on:keyup.enter="sendEmail"></a-input>
              <span class="send-span" @click="sendEmail">{{sendMsg}}</span>
            </div>
            <span class="email-message" :class="{'email-unbind': !sendResult }" v-if="isSend">{{sendResult ? $t('Login.Email-Sent') : $t('Login.Email-No-Verified')}}</span>
            <span class="email-message email-unbind" v-if="!isSend && tipsMsg.length > 0">{{tipsMsg}}</span>
          </div>
        </div>
      </div>
      <p class="bottom-span">{{$t('Login.If-Cannot-Solved')}}<a @click="openZw">{{$t('Login.Contact-Customer-Service')}}</a>。 </p>
    </div>
  </div>
</template>

<script>
  import animationData from "../assets/lottie/au.json";

  export default {
    name: 'Login',
    data() {
      return {
        address: '',
        email: '',
        defaultOptions: {
          animationData: animationData,
        },
        animationSpeed: 6,
        anim: {},
        sendMsg: this.$t('Login.Sent'),
        sendResult: false,
        tipsMsg: '',
        isSend: false,
        height: this.$height(),
        countTime: 0,
        secondNum: 60,
        isPc: this.$isPc(),
        isBrowser: true,
        isEmail: true,
        canSwitch: true,
      };
    },
    destroyed() {
      window.clearInterval(this.countTime);
    },
    mounted() {
      if (this.$route.query.email) {
        this.email = this.$route.query.email
      }
    },
    methods: {
      modeSwitch(isBrowser) {
        this.isBrowser = isBrowser;
        //if (isBrowser) this.clearTipsMsg();
      },
      clearTipsMsg() {
        this.tipsMsg = '';
        this.isSend = false;
      },
      countDown() {
        if (this.countTime === 0) {
          this.countTime = setInterval(() => {
            this.secondNum--
            this.sendMsg = '(' + this.secondNum + ')';
            if (this.secondNum === 0) {
              this.secondNum = 60;
              window.clearInterval(this.countTime);
              this.countTime = 0;
              this.sendMsg = this.$t('Login.Resent');
            }
          }, 1000);
        }
      },
      sendEmail() {
        if (this.sendMsg === this.$t('Login.Sent') || this.sendMsg === this.$t('Login.Resent')) {
          this.isSend = false;
          const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
          if (this.email) {
            if (emailReg.test(this.email)) {
              this.countDown();
              const that = this;
              this.$request.auLoginWithEmail(this.email, function (data) {
                that.isSend = true;
                //that.sendMsg = '重发';
                if (data && (data.status === 200 || data.status === 201) && data.data) {
                  that.sendResult = data.data.result === "succeed";
                  if (!that.sendResult) {
                    window.clearInterval(that.countTime);
                    that.sendMsg = that.$t('Login.Resent');
                  }
                } else {
                  window.clearInterval(that.countTime);
                  that.countTime = 0;
                  that.sendMsg = that.$t('Login.Resent');
                  that.sendResult = false;
                }
              })
            } else {
              this.tipsMsg = this.$t('Login.Enter-Valid-Email');
            }
          } else {
            this.tipsMsg = this.$t('Login.Enter-Valid-Email');
          }
        }
      },
      handleAnimation: function (anim) {
        this.anim = anim;
        //console.log(anim); //这里可以看到 lottie 对象的全部属性
      },
      openZw() {
        window.zE('webWidget', 'open');
        setTimeout(function () {
          // Open the channel
          document.getElementsByClassName('zEWidget-webWidget--active')[0].contentWindow.document.getElementsByTagName('footer')[0].getElementsByTagName('button')[0].click();
        }, 1000);
      },
    }
  };
</script>

